import logger from '@ali/4ever-logger';
import Table from '../../mo/models';

export function logNPEInfo(table: Table, info: Record<string, any>) {
  try {
    const tableRowColData = table.nodes.map((row) => {
      const rowData = row.nodes.map((cell) => {
        if (cell.data?.hidden) {
          return 'hidden';
        }
        return `${cell.data?.rowSpan || 1}-${cell.data?.colSpan || 1}`;
      });
      return rowData;
    });
    logger.info({
      type: 'table_npe',
      info: JSON.stringify(info),
      tableRowColData: JSON.stringify(tableRowColData),
    });
  // eslint-disable-next-line no-empty
  } catch (e) {}
}
